<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Marketing activities</h1>
      <div class="chartWrap">
        <a href="javascript:void(0)" @click="$router.push('search')" class="iconfont cart"> &#xe625; </a>
      <span class="bag"> <em class="font10"> {{indexData.shoppingCartNum}} </em> </span>
        <a href="javascript:void(0)" @click="$router.push('/cart')" class="iconfont cart"> &#xe669; </a></div>
    </section>
    <div class="center">
        <ul>
            <li v-for="(item,index) in activities" :key="index" @click="$router.push(item.jumpUrl)">
                <div class="photoWrap">
                    <img :src="item.img1">
                    <div class="discount">
                        <span>{{item.name}}</span>
                        <div>UP to {{item.discountRate}}off</div>
                    </div>
                </div>
            </li>
        </ul>
    </div>

</div>
</template>

<script>

import {getMarketingActivity} from '@/api/flashSale'
import {mapGetters} from 'vuex'
export default {
  name: 'activities',
  data () {
    return {
     control: false,
      activities:[]
    
    }
  },
  computed: {
      ...mapGetters(['indexData'])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
        getMarketingActivity().then(res=>{
            this.activities=res.data.result;
        })
    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    
  }
}
</script>

<style lang="scss" scoped>
   .center{
       width: 100%;
       
   }
   ul{
    border-top: 10px solid #f6f6f6;
   }
   li{
        width: 100%;
        padding: 10px 4% 0;
   }
   .photoWrap{
       width: 100%;
       position: relative;
       height: 105px;
       img{
           width: 100%;
           height: 100%;
       }
       .discount{
           position: absolute;
           right: 10px;
           top: 27px;
           span{
               font-size: 12px;
               color: white;
           }
           div{
               font-size: 13px;
               background-color: #ed4236;
               color: white;
               margin-top: 10px;
           }
       }
   }
</style>






